// Storybook styling
.icons-demo {
  display: flex;
  flex-wrap: wrap;

  .icon {
    height: 100px;
    padding: $space;
    width: 100px;

    @media (prefers-color-scheme: dark) {
      fill: clr(gray);
    }
  }

  pre {
    background-color: clr(gray-lightest);
    font-size: 0.8rem;
    margin: 0;
    text-align: center;
  }
}

.icons-demo__item {
  border: 1px solid clr(gray-lightest);
  margin: $space-4;
}
